import { Box, Grid, Modal, ModalClose, ModalDialog } from "@mui/joy";
import _ from "lodash";
import React, {
  createContext,
  memo,
  useCallback,
  useEffect,
  useState,
} from "react";
import {
  AnLink,
  BaseCard,
  BaseImage,
  dialogModal,
  goRoot,
  goTerms,
  GrandButton,
  GridIt,
  LotAnim,
  OutButton,
  translateTexts,
  UnList,
} from "./utils";

const d100 = "20px";
const d104 = "45px";
const d105 = "50px";
const d115 = "60px";
const d112 = "70px";
const d120 = "150px";
const d125 = "210px";
const d130 = "330px";
const cloudSrc =
  "https://user-images.githubusercontent.com/284717/221367562-dd20ce96-4cfd-4320-afe3-91b51fbe5a5b.png";
const showTime = 300;
const modal1 = 1;
const trans2 = 2;
const trans3 = 3;
const trans4 = 4;

const modalTexts = [
  [
    {
      t: 1,
      w: "Send an mail to backstage@roobet.com with your Roobet username, one reason why you want to meet Snoop Dog, and your preferred tour location",
    },
    {
      t: 1,
      w: "Five lucky Roobetters and their +1s will win a backstage pass to meet Snoop",
    },
    { t: 1, w: "Minimum lifetime deposit of $10 required" },
  ],
];

const titleMed = {
  fontSize: d105,
  "@media (max-width: 600px)": {
    fontSize: d104,
  },
};

const titleSec = {
  fontSize: d112,
  "@media (max-width: 600px)": {
    fontSize: d105,
  },
};

const titleYe = {
  color: "#ddb43f",
  ...titleSec,
};

const medTitle = {
  fontSize: "100px",
  "@media (max-width: 600px)": {
    fontSize: d104,
  },
  lineHeight: 0.9,
  color: "black",
};

const bigTitle = {
  fontWeight: "bold",
  fontSize: "130px",
  "@media (max-width: 600px)": {
    fontSize: d115,
  },
  lineHeight: 0.9,
  color: "black",
};

const whiteDiff = {
  mixBlendMode: "difference",
  ...bigTitle,
  color: "white",
};

// https://gist.github.com/marcosvpj/768780cf6092e4b59c332df33a7bbc61
const primeContainer = {
  maxWidth: "100ch",
  padding: "1ch",
  margin: "auto",
};

// TODO use from ./utils
const unList = {
  margin: 0,
  fontSize: "initial",
};

const PageContext = createContext({ setModalValue: (x) => x });

const DiffNormal = ({ children, sx }) => (
  <Box
    sx={{
      color: "white",
      mixBlendMode: "difference",
      ...sx,
    }}
  >
    {children}
  </Box>
);

const HowItems = ({
  children,
  actionFile,
  termsFile,
  modalVal,
  setModalVal,
}) => {
  return (
    <>
      {children}
      <GridIt>
        <Grid container spacing={2} alignItems="center">
          <GridIt>
            <GrandButton
              myStyle={{
                "@media (max-width: 600px)": {
                  padding: "15px 20px 15px 20px",
                },
              }}
              onClick={() => {
                if (modalVal) {
                  setModalVal(modalVal);
                  return;
                }

                window.open(`https://roobet.com/${actionFile}`, "_blank");
              }}
            >
              {modalVal > 0 ? "HOW TO ENTER" : "LET'S GO"}
            </GrandButton>
          </GridIt>
          <GridIt>
            <OutButton
              variant="outlined"
              color="warning"
              myStyle={{
                color: "white",
                "@media (max-width: 600px)": {
                  padding: "15px",
                },
              }}
              onClick={() => goTerms(termsFile)}
            >
              TERMS
            </OutButton>
          </GridIt>
        </Grid>
      </GridIt>
    </>
  );
};

const CloudBase = ({
  top,
  onLoad,
  // Optional props
  myStyle = {},
  myClass = "",
  isBig = false,
}) => (
  <BaseImage
    className={myClass.length > 0 ? myClass : undefined}
    alt="cloud"
    src={cloudSrc}
    onLoad={onLoad}
    style={{
      width: isBig ? "150%" : "100%",
      minWidth: "800px",
      position: "absolute",
      top,
      left: "50%",
      transform: "translateX(-50%)",
      zIndex: -1,
      ...myStyle,
    }}
  />
);

const MainCard = ({
  src,
  children,
  styleBillboard,
  termsFile,
  setModalVal,
  // Optional props
  actionFile = "",
  isSmTop = false,
  modalVal = 0,
}) => (
  <BaseCard isDark>
    <>
      <Box
        sx={{
          display: { xs: "block", lg: "none" },
          padding: d100,
          marginTop: d105,
        }}
      >
        <Grid container spacing={3} direction="column">
          <HowItems
            actionFile={actionFile}
            termsFile={termsFile}
            modalVal={modalVal}
            setModalVal={setModalVal}
          >
            {children}
          </HowItems>
          <GridIt sx={{ textAlign: "right" }}>
            <img
              alt="img"
              src={src}
              style={{
                width: "280px",
                marginRight: "-20px",
                marginBottom: "-26px",
              }}
            />
          </GridIt>
        </Grid>
      </Box>
      <Box
        sx={{
          display: { xs: "none", lg: "block" },
          padding: d115,
          marginTop: isSmTop ? d115 : "95px",
          position: "relative",
        }}
      >
        <Grid container alignItems="center">
          <GridIt xs={7}>
            <Grid container spacing={3} direction="column">
              <HowItems
                actionFile={actionFile}
                termsFile={termsFile}
                modalVal={modalVal}
                setModalVal={setModalVal}
              >
                {children}
              </HowItems>
            </Grid>
          </GridIt>
          <GridIt xs={5}>
            <img
              alt="img"
              src={src}
              style={{
                position: "absolute",
                right: 0,
                ...styleBillboard,
              }}
            />
          </GridIt>
        </Grid>
      </Box>
    </>
  </BaseCard>
);

const Dope = () => {
  const [isIntro, setIsIntro] = useState(false);
  const [modalValue, setModalValue] = useState(0);
  const [cloud1Val, setCloud1Val] = useState("0px");
  const [cloud2Val, setCloud2Val] = useState("0px");
  const [cloud3Val, setCloud3Val] = useState("0px");
  const [cloud4Val, setCloud4Val] = useState("0px");
  const [cloud5Val, setCloud5Val] = useState("0px");
  const [transHow, setTransHow] = useState(["", ""]);

  const backStage = transHow[1].split("backstage@roobet.com");
  const snoopStage = transHow[1].split("snoop@roobet.com");

  const closeIntro = () => {
    if (isIntro === true) {
      return;
    }

    // @ts-ignore
    document.getElementById("myAudio").play();
    setIsIntro(true);
  };

  const resizeBase = () => {
    const ornTop = document.querySelector(".ornamentTop");
    const cloud1 = document.querySelector(".cloud1");
    const cloud5 = document.querySelector(".cloud5");
    if (ornTop == null || cloud1 == null || cloud5 == null) {
      return;
    }
    const rectValues = ornTop.getBoundingClientRect();
    const cloud1Rect = cloud1.getBoundingClientRect();
    const cloud5Rect = cloud5.getBoundingClientRect();
    const myVal = window.pageYOffset + rectValues.bottom;

    setCloud1Val(`${myVal - cloud1Rect.height * 0.75}px`);
    setCloud2Val(`${myVal - cloud5Rect.height * 0.7}px`);
    setCloud5Val(`${myVal - cloud5Rect.height * 0.4}px`);
    setCloud4Val(`${myVal - cloud5Rect.height * 0.4}px`);
    setCloud3Val(`${myVal - cloud5Rect.height * 0.2}px`);
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleResize = useCallback(_.debounce(resizeBase, showTime), []);

  useEffect(() => {
    if (modalValue === 0) {
      return;
    }

    const translateValues = async () => {
      const transTexts = await translateTexts(modalTexts, modalValue);
      // @ts-ignore
      setTransHow(transTexts);
    };

    translateValues();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [modalValue]);

  useEffect(() => {
    const eleVideo = document.querySelector("video.videoBack");
    if (eleVideo != null) {
      // @ts-ignore
      eleVideo.playbackRate = 0.6;
    }

    window.addEventListener("resize", handleResize);
    handleResize();
    return () => window.removeEventListener("resize", handleResize);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <PageContext.Provider value={{ setModalValue }}>
      <audio id="myAudio">
        <source
          src="https://cdn.jsdelivr.net/gh/violet-guru/f/b/dope_gin.mp3"
          type="audio/mpeg"
        />
        Your browser does not support the audio element.
      </audio>
      <video is="x-muted" muted class="videoBack" playsinline autoPlay loop>
        <source
          type="video/mp4"
          src="https://user-images.githubusercontent.com/284717/221643838-0d81645d-4514-4929-bad9-6664efb21e4a.mp4"
        />
        Your browser does not support the video tag.
      </video>
      <CloudBase
        myClass="cloud1"
        top={cloud1Val}
        isBig
        myStyle={{ opacity: 0.25 }}
        onLoad={handleResize}
      />
      <CloudBase
        top={cloud2Val}
        myStyle={{ opacity: 0.4 }}
        onLoad={handleResize}
      />
      <CloudBase top={cloud3Val} onLoad={handleResize} />
      <CloudBase top={cloud4Val} onLoad={handleResize} />
      <CloudBase myClass="cloud5" top={cloud5Val} onLoad={handleResize} />
      <Box sx={primeContainer}>
        <Box sx={{ textAlign: "center", position: "relative" }}>
          <BaseImage
            alt="burst"
            src="https://user-images.githubusercontent.com/284717/221367564-185bf50d-7e87-4625-aa01-7a31cfa9c0d7.svg"
            onLoad={handleResize}
            style={{
              width: "110%",
              minWidth: "600px",
              position: "absolute",
              bottom: 0,
              left: "50%",
              transform: "translateX(-50%)",
              zIndex: -1,
              background:
                "radial-gradient(circle, rgb(44, 44, 84, 0.5) 20%, rgba(0,0,0, 0) 50%)",
            }}
          />
          <BaseImage
            alt="cross"
            src="https://user-images.githubusercontent.com/284717/221385928-b579ba57-997e-4e4f-97a0-76bf24479912.png"
            onLoad={handleResize}
            style={{ width: "280px" }}
          />
          <BaseImage
            className="ornamentTop"
            alt="man"
            src="https://user-images.githubusercontent.com/284717/223543777-dc493a9c-cf7b-4e62-93d3-647cfc7a71c4.png"
            onLoad={handleResize}
            style={{ width: "53%", minWidth: d130 }}
          />
        </Box>
        <Box sx={{ textAlign: "center", marginTop: "-40px" }}>
          <Box sx={medTitle}>WELCOME</Box>
          <Box sx={bigTitle}>SNOOP</Box>
          <Box sx={bigTitle}>DOGG</Box>
          <GrandButton sx={{ marginTop: d100 }} onClick={goRoot}>
            JOIN THE CELEBRATION
          </GrandButton>
          <Box
            sx={{
              marginTop: { xs: d125, lg: "380px" },
              ...whiteDiff,
            }}
          >
            SNOOP EXCLUSIVES
          </Box>
          <DiffNormal sx={{ fontSize: "initial", marginTop: d105 }}>
            Wanna meet Snoop Dogg?<span onClick={closeIntro}> Play </span>these
            events for a chance to win an opportunity of a lifetime.
          </DiffNormal>
        </Box>
        <MainCard
          src="https://user-images.githubusercontent.com/284717/221220639-ae8f2a76-c6b5-471e-9c7b-6864e09b31bb.png"
          styleBillboard={{ bottom: "0", width: "520px" }}
          termsFile="make-it-rain-with-snoop"
          setModalVal={setModalValue}
          isSmTop
        >
          <GridIt>April 10th - May 7th</GridIt>
          <GridIt>
            <Box sx={titleMed}>Make it Rain</Box>
            <Box sx={titleYe}>With Snoop Dogg</Box>
          </GridIt>
          <GridIt>
            Its raining cash with the Roobet raffle - $420,000 up for grabs.
          </GridIt>
          <GridIt>
            <ul style={unList}>
              <li>Grab one ticket per $500 wagered</li>
              <li>1,000 winners with $100,000 grand prize</li>
              <li>
                Earn 5x more tickets while playing{" "}
                <a
                  style={{ textDecoration: "revert" }}
                  href="https://roobet.com/snoops-hotbox"
                  target="_blank"
                  rel="noreferrer"
                >
                  Snoops HotBox
                </a>
              </li>
            </ul>
          </GridIt>
        </MainCard>
        <MainCard
          src="https://user-images.githubusercontent.com/284717/228624510-711ef11c-692f-4c2e-87e4-cb4ddffc305b.png"
          styleBillboard={{ bottom: "0", width: "390px" }}
          actionFile=""
          termsFile="backstage-with-snoop"
          modalVal={1}
          setModalVal={setModalValue}
        >
          <GridIt>April 24th - May 10th</GridIt>
          <GridIt>
            <Box sx={titleMed}>Backstage</Box>
            <Box sx={titleYe}>With Snoop</Box>
          </GridIt>
          <GridIt>
            Win an exclusive backstage pass to meet Snoop Dogg during his tour!
          </GridIt>
        </MainCard>
        <MainCard
          src="https://user-images.githubusercontent.com/284717/228618338-07ecbb1e-bd6c-401c-af8c-cd640218be5e.png"
          styleBillboard={{ bottom: "0", width: "530px" }}
          actionFile="tag/roobet-bj"
          termsFile="on-the-wheel-with-420"
          setModalVal={setModalValue}
        >
          <GridIt>April 20th - April 27th</GridIt>
          <GridIt>
            <Box sx={titleMed}>On the Wheel</Box>
            <Box sx={titleYe}>With 420</Box>
          </GridIt>
          <GridIt>Witness the prize wheel spinning.</GridIt>
          <GridIt>
            <ul style={unList}>
              <li>Each day at 04:20pm EST a table will be randomly selected</li>
              <li>
                Play on that table to win free spins, bonus buys, cash, and free
                bets
              </li>
              <li>$5 minimum bet to qualify</li>
            </ul>
          </GridIt>
        </MainCard>
        <Box sx={{ textAlign: "center" }}>
          <BaseImage
            alt="img"
            src="https://user-images.githubusercontent.com/284717/221205296-3f4b2407-e0e7-4305-a274-d237d047918c.png"
            style={{
              width: "47%",
              minWidth: d130,
              mixBlendMode: "difference",
              marginTop: d120,
            }}
          />
          <DiffNormal sx={{}}>More events coming soon.</DiffNormal>
        </Box>
      </Box>
      <Box
        sx={{
          marginTop: "-60px",
          textAlign: "center",
          backgroundImage:
            "url(https://user-images.githubusercontent.com/284717/221382860-cc19da68-76ca-4ff3-badb-bfa3711635a7.svg), " +
            "linear-gradient(to bottom, rgba(0,0,0,0), black 53%)",
          backgroundSize: "contain",
          "@media (max-width: 600px)": {
            backgroundSize: "cover",
          },
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center bottom",
        }}
      >
        <Box sx={primeContainer}>
          <Box sx={{ marginTop: d125, fontWeight: "bold", fontSize: d104 }}>
            GOOD LUCK. HAVE FUN.
          </Box>
          <DiffNormal sx={{ fontSize: "initial", marginTop: "30px" }}>
            Let the good times roll, but gamble with control.
          </DiffNormal>
          <GrandButton
            sx={{ marginTop: "40px", marginBottom: d112 }}
            onClick={goRoot}
          >
            HOP IN
          </GrandButton>
        </Box>
      </Box>

      <Modal
        open={modalValue > 0}
        onClose={() => {
          setModalValue(0);
        }}
      >
        <ModalDialog
          size="lg"
          variant="outlined"
          sx={{
            background: "linear-gradient(to bottom, lightgray, white 65%)",
            ...dialogModal,
          }}
        >
          <ModalClose />
          <Grid
            container
            spacing={3}
            alignItems="center"
            direction="column"
            sx={{ marginTop: "7px", marginBottom: d100 }}
          >
            <GridIt>
              <LotAnim
                src="https://cdn.jsdelivr.net/gh/violet-guru/f/b/dope_lottie.json"
                style={{ width: "310px" }}
              />
            </GridIt>
            <GridIt sx={titleMed}>{transHow[0]}</GridIt>
            <GridIt>
              <UnList>
                <li>
                  {modalValue === modal1 ? (
                    <>
                      {backStage[0]}
                      <AnLink href="mailto:backstage@roobet.com">
                        backstage@roobet.com
                      </AnLink>
                      {backStage[1]}
                    </>
                  ) : (
                    <>
                      {snoopStage[0]}
                      <AnLink href="mailto:snoop@roobet.com">
                        snoop@roobet.com
                      </AnLink>
                      {snoopStage[1]}
                    </>
                  )}
                </li>
                <li>{transHow[trans2]}</li>
                <li> {transHow[trans3]}</li>
              </UnList>
            </GridIt>
            <GridIt onClick={goRoot}>
              <GrandButton>{transHow[trans4]}</GrandButton>
            </GridIt>
          </Grid>
        </ModalDialog>
      </Modal>
    </PageContext.Provider>
  );
};

export default memo(Dope);
