import React, { memo } from "react";
import Modal from "@mui/joy/Modal";
import ModalDialog from "@mui/joy/ModalDialog";
import ModalClose from "@mui/joy/ModalClose";
import Grid from "@mui/joy/Grid";
import { goPromotionsLocation, GrandButton, GridIt } from "./utils";

const Invalid = () => {
  const expireEl = document.querySelector(".end-date-promo");

  return (
    <>
      {expireEl != null &&
        expireEl.innerText.length > 0 &&
        new Date(
          `${expireEl.innerText.substring(0, 10)}T${expireEl.innerText
            .split(" ")[1]
            .padStart(5, "0")}:00.000-05:00`
        ) < new Date() && (
          <Modal
            open={true}
            onClose={goPromotionsLocation}
            sx={{
              transform: "translate3d(0, 0, 20000px)",
              zIndex: 99999,
            }}
          >
            <ModalDialog
              size="lg"
              variant="outlined"
              sx={{ textAlign: "center", padding: "40px" }}
            >
              <ModalClose />
              <Grid
                container
                spacing={2}
                alignItems="center"
                direction="column"
              >
                <GridIt sx={{ fontSize: "xx-large" }}>
                  This promotion has expired
                </GridIt>
                <GridIt>
                  <GrandButton onClick={goPromotionsLocation}>
                    MORE PROMOTIONS
                  </GrandButton>
                </GridIt>
              </Grid>
            </ModalDialog>
          </Modal>
        )}
    </>
  );
};

export default memo(Invalid);
