import React, { memo } from "react";
import UseSlide from "./UseSlide";

const BigSlide = ({ isWidest, src, children, isPage }) => {
  return (
    <UseSlide className="mySlide videoWrapper" isWidest={isWidest}>
      <video
        is="x-muted"
        muted
        class="videoBack"
        playsInline
        autoPlay
        loop
        // https://github.com/alvarotrigo/fullPage.js#pause-on-leave
        data-keepplaying
        style={{ display: isPage ? "block" : "none" }}
      >
        <source type="video/mp4" src={src} />
        Your browser does not support the video tag.
      </video>
      {children}
    </UseSlide>
  );
};

export default memo(BigSlide);
