/* eslint-disable no-undef */
import Box from "@mui/joy/Box";
import React, { useEffect, useState } from "react";
import Initial from "./Initial";
import Advent from "./Advent";
import Moreno from "./Moreno";
import Carnival from "./Carnival";
import Dope from "./Dope";
import Dagga from "./Dagga";
import Chara from "./Chara";
import Meta from "./Meta";
import Rooyale from "./Rooyale";
import Jack from "./Jack";
import Fame from "./Fame";
import Birth from "./Birth";
import Hepta from "./Hepta";

const morenoVal = 2;
const carniVal = 4;
const dopeVal = 5;
const daggaVal = 6;
const charaVal = 7;
const metaVal = 8;
const rooyaleVal = 9;
const jackVal = 10;
const fameVal = 11;
const birthVal = 12;
const heptaVal = 13;

const checkUrl = () => {
  console.log("checkUrl");

  if (isPath("/promotions/holiday-campaign-22")) {
    return 1;
  }

  if (isPath("/promotions/morenos-100k-combat-raffle")) {
    return morenoVal;
  }

  if (isPath("/promotions/carnival")) {
    return carniVal;
  }

  if (isPath("/promotions/snoop-dogg-events")) {
    return dopeVal;
  }

  if (isPath("/promotions/welcome-offer1")) {
    return daggaVal;
  }

  if (isPath("/promotions/roo-rocks-cosmos")) {
    return charaVal;
  }

  if (isPath("/promotions/metaverse-marathon")) {
    return metaVal;
  }

  if (isPath("/promotions/casino-rooyale")) {
    return rooyaleVal;
  }

  if (isPath("/promotions/all-in-august")) {
    return jackVal;
  }

  if (isPath("/promotions/roos-spin-score-september")) {
    return fameVal;
  }
  
  if (isPath("/promotions/roos-fifth-anniversary")) {
    return birthVal;
  }

  if (isPath("/promotions/welcome-week")) {
    return heptaVal;
  }

  //return -1;
};

const isPath = (x) => window.location.href.indexOf(x) > 0;

const AllPages = ({ langValue, putLang, pageIndex }) => {
  if (pageIndex === morenoVal) {
    return <Moreno />;
  }
  if (pageIndex === dopeVal) {
    return <Dope />;
  }
  if (pageIndex === daggaVal) {
    return <Dagga />;
  }
  if (pageIndex === fameVal) {
    return <Fame />;
  }
  if (pageIndex === birthVal) {
    return <Birth />;
  }

  return (
    <Box
      // https://gist.github.com/marcosvpj/768780cf6092e4b59c332df33a7bbc61
      sx={{
        maxWidth: "100ch",
        padding: pageIndex === 0 ? "2ch" : "1ch",
        margin: "auto",
        textAlign: pageIndex === 0 ? "center" : undefined,
      }}
    >
      {pageIndex === 0 && <Initial langValue={langValue} putLang={putLang} />}
      {pageIndex === 1 && <Advent />}
      {pageIndex === carniVal && <Carnival />}
      {pageIndex === charaVal && <Chara />}
      {pageIndex === metaVal && <Meta />}
      {pageIndex === rooyaleVal && <Rooyale />}
      {pageIndex === jackVal && <Jack />}
      {pageIndex === heptaVal && <Hepta />}
    </Box>
  );
};

const AppBase = ({ pageIndex }) => {
  const [langValue, setLangValue] = useState();

  const putLang = (_, actualLang) => {
    Weglot.switchTo(actualLang);
    setLangValue(actualLang);
  };

  useEffect(() => {
    if (pageIndex === 0) {
      Weglot.on("initialized", () => {
        // get the current active language
        let currentLang = Weglot.getCurrentLang();

        const urlParams = Object.fromEntries(
          new URLSearchParams(window.location.search).entries()
        );
        if (urlParams.hl != null) {
          currentLang = urlParams.hl;
          Weglot.switchTo(currentLang);
        }

        setLangValue(currentLang);
      });
    } else {
      document.body.classList.add("alteredBody");

      // Hide wf element
      document.querySelectorAll("#root + .blog-hero-section").forEach((el) => {
        // @ts-ignore
        el.style.display = "none";
      });

      document
        .querySelectorAll(".blog-hero-section + .blog-post-rich-text-section")
        .forEach((el) => {
          // @ts-ignore
          el.style.display = "none";
        });

      document
        .querySelectorAll(".cta-section + .terms-section-modal")
        .forEach((el) => {
          // @ts-ignore
          el.style.display = "none";
        });
    }

    if (pageIndex === morenoVal) {
      document.querySelectorAll(".navbar.w-nav").forEach((el) => {
        // @ts-ignore
        el.style.display = "none";
      });

      document
        .querySelectorAll(".terms-section + .cta-section")
        .forEach((el) => {
          // @ts-ignore
          el.style.display = "none";
        });

      //https://stackoverflow.com/a/66712689/6227407
      const ele = document.querySelector(".section.footer");
      if (ele != null) {
        const moveTo = document.querySelector(".appendFooter");
        // @ts-ignore
        moveTo.parentNode.insertBefore(ele, moveTo);
      }

      const eleWrapper = document.querySelector(".wg-element-wrapper.sw6");
      if (eleWrapper != null) {
        // @ts-ignore
        eleWrapper.style.opacity = 1;
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <AllPages langValue={langValue} putLang={putLang} pageIndex={pageIndex} />
  );
};

const App = () => {
  const pageIndex = checkUrl();

  if (pageIndex > 0) {
    return <AppBase pageIndex={pageIndex} />;
  }

  return <></>;
};

export default App;
