const Carnival = () => {
  const [heightVal, setHeightVal] = useState(a105);
  const [heightSecond, setHeightSecond] = useState(a105);
  const [preDay, setPreDay] = useState(-1);
  const [transGift, setTransGift] = useState();
  const [transTitle, setTransTitle] = useState();
  const [transChild, setTransChild] = useState();
  const [transUndefs, setTransUndefs] = useState([]);
  const [transNow, setTransNow] = useState();
  const [transTerms, setTransTerms] = useState();
  const [selectedDay, setSelectedDay] = useState(-1);
  const [liveModal, setLiveModal] = useState(true);

  const loveDay = [3, 4].includes(selectedDay);
  const dayIndex = new Date().getDate() - dayDifference;
  const slideIndex = dayIndex >= 0 ? dayIndex : 0;

  const handleResize = () => {
    const ele = document.querySelector(".ornamentTop");
    if (ele == null) {
      return;
    }
    const rectValues = ele.getBoundingClientRect();
    setHeightVal(
      `${window.pageYOffset + rectValues.top + 70 - rectValues.x / 5}px`
    );

    const eleSecond = document.querySelector(".ornamentBottom");
    if (eleSecond == null) {
      return;
    }

    const rectObj = eleSecond.getBoundingClientRect();
    // https://stackoverflow.com/a/50310297/6227407
    setHeightSecond(`${window.pageYOffset + rectObj.top}px`);
  };

  useEffect(() => {
    window.addEventListener("resize", handleResize);
    handleResize();
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    if (preDay < 0) {
      setSelectedDay(preDay);
      return;
    }

    const translateTexts = async () => {
      try {
        const currentLang = Weglot.getCurrentLang();
        const payLoad = {
          words: [
            { t: 1, w: `Feb. ${preDay + dayDifference}` },
            { t: 1, w: prodList[preDay].titleVal },
            { t: 1, w: prodList[preDay].childVal },
            { t: 1, w: prodList[preDay].undefList[0] },
            { t: 1, w: prodList[preDay].undefList[1] },
            { t: 1, w: prodList[preDay].undefList[2] },
            { t: 1, w: "PLAY & WIN!" },
            { t: 1, w: "TERMS" },
          ],
          languageTo: currentLang,
        };
        // Sending the same payload twice, Weglot API bug
        await Weglot.translate(payLoad, () => {});
        await Weglot.translate(payLoad, (data) => {
          setTransGift(data[0]);
          setTransTitle(data[1]);
          setTransChild(data[2]);
          setTransUndefs([data[3], data[4], data[5]]);
          setTransNow(data[6]);
          setTransTerms(data[7]);
        });
      } catch (err) {
        console.error(err);
      } finally {
        setSelectedDay(preDay);
      }
    };
    translateTexts();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [preDay]);

  useEffect(() => {
    // Trigger css animation
    setLiveModal(selectedDay >= 0);
  }, [selectedDay]);

  return (
    <>
      <Box sx={{ display: "none" }}>
        <img src={modalImage} alt="addict1" />
      </Box>
      <snowfall>
        {[...Array(snowCount).keys()].map((x, i) => (
          <snowflake key={i}>
            <img
              src={
                x < 17
                  ? "https://user-images.githubusercontent.com/284717/217290995-1d26f675-a526-4d23-bea7-098ee043e345.png"
                  : x > 34
                  ? "https://user-images.githubusercontent.com/284717/217653599-9bad3c79-8b34-4f1c-a2cb-cb91f22e44c4.png"
                  : "https://user-images.githubusercontent.com/284717/217291006-9e125692-d787-40c0-b43b-c2689da6aeed.png"
              }
              alt="addict"
            />
          </snowflake>
        ))}
      </snowfall>
      <Box
        sx={{
          position: "absolute",
          top: 0,
          left: 0,
          right: 0,
          zIndex: -1,
          background: c105,
          height: heightVal,
        }}
      ></Box>
      <Box
        sx={{
          position: "absolute",
          top: heightVal,
          left: 0,
          zIndex: -1,
        }}
      >
        <img
          src="https://user-images.githubusercontent.com/284717/217653608-12fa2697-7e84-4b23-ae1a-6df2b462f4db.png"
          alt="bottomJoin"
          style={{ width: "100%" }}
        />
      </Box>
      <Box
        sx={{
          textAlign: "center",
          color: "black",
        }}
      >
        <HueImg
          src="https://user-images.githubusercontent.com/284717/217625458-d24774b5-8f44-4284-86f8-ae5944aea79c.jpg"
          alt="addict"
          liveModal={liveModal}
          myStyle={{ width: "95%", borderRadius: a110 }}
          onLoad={handleResize}
        />
        <HueImg
          src="https://user-images.githubusercontent.com/284717/217364483-1e1ed645-439c-4a5f-8de4-51da888ba759.png"
          alt="addict"
          liveModal={liveModal}
          myStyle={{ width: "45%", minWidth: "330px" }}
          onLoad={handleResize}
        />
        <Box>
          Mr. Roo is celebrating Carnival with a new event every day in
          February. Join in on the festive fun thats going down at Roobet!
        </Box>
        <JoinButton
          className="ornamentTop"
          onClick={goRoot}
          myStyle={{ marginTop: a120, marginLeft: a110, marginRight: a110 }}
        >
          JOIN THE CELEBRATION
        </JoinButton>
      </Box>
      <Swiper
        navigation={true}
        modules={[Autoplay, Navigation]}
        {...window.slideSwiperOptions}
        style={{ marginTop: a125 }}
      >
        {window.slideSwiperList != null &&
          window.slideSwiperList.map((x, i) => (
            <React.Fragment key={`q_${i.toString()}`}>
              {x.isDateDynamic == null && (
                <SwiperSlide>
                  <BaseCard>
                    <HowGrid
                      subTitle={x.subTitle}
                      undefList={[]}
                      src={x.lottieJson}
                      termsFile={x.termsFile}
                      myStyle={x.lottieStyle}
                      playUrl={x.playUrl}
                    >
                      <Box sx={x.firstStyleSx}>{x.firstText}</Box>
                      <Box sx={x.secondStyleSx}> {x.secondText}</Box>
                    </HowGrid>
                  </BaseCard>
                </SwiperSlide>
              )}
              {x.isDateDynamic === true && (
                <SwiperSlide>
                  <BaseCard>
                    <HowGrid
                      subTitle={prodList[slideIndex].childVal}
                      undefList={prodList[slideIndex].undefList}
                      src="https://assets4.lottiefiles.com/private_files/lf30_jty7xpzm.json"
                      termsFile={prodList[slideIndex].termsFile}
                      myStyle={{ width: "400px" }}
                      playUrl="roobet.com"
                    >
                      <Box sx={{ ...luckyMed, color: c110 }}>
                        {prodList[slideIndex].titleVal}
                      </Box>
                    </HowGrid>
                  </BaseCard>
                </SwiperSlide>
              )}
            </React.Fragment>
          ))}
      </Swiper>
      <OrnamentBig top="2600px" sx={{}} />
      <Box
        sx={{
          textAlign: "center",
          marginTop: a120,
          marginBottom: a110,
        }}
      >
        <Box sx={luckyYe}>Calendar Of Events</Box>
        <Box>Check out everything that's going down this month</Box>
      </Box>

      <BaseCard>
        <Grid
          container
          alignItems="center"
          justifyContent="center"
          sx={{
            padding: a105,
          }}
        >
          {[...Array(prodList.length).keys()].map((x, i) => (
            <TamElement key={`q_${i.toString()}`} i={x} setPreDay={setPreDay} />
          ))}
        </Grid>
      </BaseCard>
      <Box
        className="ornamentBottom"
        sx={{
          borderRadius: a110,
          padding: a120,
          textAlign: "center",
          color: "black",
          backgroundColor: c105,
          marginTop: a120,
        }}
      >
        <HueImg
          src="https://user-images.githubusercontent.com/284717/217600168-aae69fe1-e064-4ea9-9fb9-5663dbe7876c.png"
          alt="addict"
          liveModal={liveModal}
          myStyle={{ width: "45%", minWidth: "205px" }}
          onLoad={handleResize}
        />
        <Box>
          Move over Rio. Mr. Roo's Carnival is looking more inviting than ever!
        </Box>
        <JoinButton myStyle={{ marginTop: a120 }} onClick={goRoot}>
          LET'S DANCE
        </JoinButton>
      </Box>
      <OrnamentBig top={heightSecond} sx={{ zIndex: undefined }} />
      <Modal
        open={selectedDay >= 0}
        onClose={() => {
          setPreDay(-1);
        }}
      >
        <ModalDialog size="lg" variant="outlined" sx={dialogModal}>
          <ModalClose />
          <GiftIn
            sx={{
              opacity: 1,
              fontSize: liveModal ? luckyBig.fontSize : undefined,
            }}
          >
            {transGift}
          </GiftIn>
          <Box sx={luckyMed}>{transTitle}</Box>
          <img
            src={modalImage}
            alt="addict1"
            style={{
              width: "100%",
              minWidth: "308px",
              filter: liveModal ? undefined : "hue-rotate(2400deg)",
              transition: "10s linear",
            }}
          />
          <Box sx={{ marginTop: a110 }}>
            <Box>{transChild}</Box>
            <UnList>
              {transUndefs.map((x, i) => (
                <li key={`q_${i.toString()}`}>{x}</li>
              ))}
            </UnList>
          </Box>
          <Grid container spacing={2} justifyContent="center">
            <GridIt>
              <GrandButton onClick={goRoot}>{transNow}</GrandButton>
            </GridIt>
            <GridIt>
              <OutButton
                variant="outlined"
                color="warning"
                onClick={() => goTerms(prodList[selectedDay].termsFile)}
              >
                {transTerms}
              </OutButton>
            </GridIt>
          </Grid>
          {/* http://thenewcode.com/860/Animating-Elements-In-Arcs-Circles-and-Ellipses-With-CSS */}
          <Box
            sx={{
              animation: "circle 7s infinite linear",
              left: "200px",
              ...ticketRide,
            }}
          >
            <LotAnim
              src={
                loveDay
                  ? "https://assets4.lottiefiles.com/packages/lf20_owvgniro.json"
                  : "https://assets7.lottiefiles.com/packages/lf20_bco260cj.json"
              }
              speed={loveDay ? fastSpeed : 1}
              style={{ width: "200px" }}
            />
          </Box>
          <Box
            sx={{
              animation: "7s infinite linear",
              animationName: loveDay ? "circle" : "clock",
              right: "-370px",
              ...ticketRide,
            }}
          >
            <LotAnim
              src={
                loveDay
                  ? "https://assets4.lottiefiles.com/packages/lf20_owvgniro.json"
                  : "https://assets3.lottiefiles.com/packages/lf20_n0jeixzn.json"
              }
              speed={loveDay ? fastSpeed : 1}
              style={{ width: "200px" }}
            />
          </Box>
        </ModalDialog>
      </Modal>
    </>
  );
};

