import React, { memo, useEffect, useState } from "react";
import {
  AnLink,
  BaseCard,
  BaseImage,
  GrandButton,
  GridIt,
  LotAnim,
  OutButton,
  UnList,
  dialogModal,
  goRoot,
  goTerms,
  translateTexts,
  unList,
} from "./utils";
import { Box, Grid, Modal, ModalClose, ModalDialog } from "@mui/joy";

const f20 = "20px";
const f40 = "40px";
const f45 = "45px";
const f50 = "50px";
const f60 = "60px";
const f80 = "80px";
const f100 = "100px";
const f130 = "130px";
const f200 = "200px";
const modal1 = 1;
const trans2 = 2;
const trans3 = 3;
const trans4 = 4;
const emptyRocket =
  "https://user-images.githubusercontent.com/284717/234650893-076f711a-0ad2-4f4c-86c7-7944db30eba8.png";
const fillRocket =
  "https://user-images.githubusercontent.com/284717/234671627-1095bfc0-f992-42ce-af5f-94f48d4614eb.png";
const buttonXs = {
  "@media (max-width: 600px)": {
    padding: "15px 20px 15px 20px",
  },
};

const modalTexts = [
  [
    {
      t: 1,
      w: "Send an mail to backstage@roobet.com with your Roobet username, one reason why you want to meet Snoop Dog, and your preferred tour location",
    },
    {
      t: 1,
      w: "Five lucky Roobetters and their +1s will win a backstage pass to meet Snoop",
    },
    { t: 1, w: "Minimum lifetime deposit of $10 required" },
  ],
];

const titleSec = {
  fontSize: f50,
  "@media (max-width: 600px)": {
    fontSize: f40,
  },
};

const titleYe = {
  color: "#ddb43f",
  ...titleSec,
  fontSize: f60,
};

const HowItems = ({
  children,
  actionFile,
  termsFile,
  buttonVal,
  modalVal,
  setModalVal,
}) => {
  return (
    <>
      {children}
      <GridIt>
        <Grid container spacing={2} alignItems="center">
          <GridIt>
            <GrandButton
              myStyle={buttonXs}
              onClick={() => {
                if (modalVal != null && modalVal > 0) {
                  setModalVal(modalVal);
                  return;
                }

                window.open(
                  `https://roobet.com/${actionFile}${window.location.search}`,
                  "_blank"
                );
              }}
            >
              {buttonVal}
            </GrandButton>
          </GridIt>
          <GridIt>
            <OutButton
              variant="outlined"
              color="warning"
              myStyle={{
                color: "white",
                ...buttonXs,
              }}
              onClick={() => goTerms(termsFile)}
            >
              TERMS
            </OutButton>
          </GridIt>
        </Grid>
      </GridIt>
    </>
  );
};

const MainCard = ({
  children,
  src,
  actionFile,
  termsFile,
  styleBillboard,
  setModalVal,
  buttonVal,
  // Optional props
  modalVal = 0,
  isBigBottom = false,
  isBigTop = false,
}) => (
  <BaseCard>
    <>
      <Box
        sx={{
          display: { xs: "block", lg: "none" },
          padding: f20,
          marginBottom: isBigBottom ? f130 : f80,
        }}
      >
        <Grid container spacing={3} direction="column">
          <HowItems
            actionFile={actionFile}
            termsFile={termsFile}
            modalVal={modalVal}
            setModalVal={setModalVal}
            buttonVal={buttonVal}
          >
            {children}
          </HowItems>
          <GridIt sx={{ textAlign: "right" }}>
            <img
              alt="img"
              src={src}
              style={{
                width: "280px",
                marginRight: "-20px",
                marginBottom: "-26px",
              }}
            />
          </GridIt>
        </Grid>
      </Box>
      <Box
        sx={{
          display: { xs: "none", lg: "block" },
          position: "relative",
          padding: f60,
          marginBottom: isBigBottom ? f200 : f80,
          marginTop: isBigTop ? f80 : 0,
        }}
      >
        <Grid container alignItems="center">
          <GridIt xs={7}>
            <Grid container spacing={3} direction="column">
              <HowItems
                actionFile={actionFile}
                termsFile={termsFile}
                modalVal={modalVal}
                setModalVal={setModalVal}
                buttonVal={buttonVal}
              >
                {children}
              </HowItems>
            </Grid>
          </GridIt>
          <GridIt xs={5}>
            <img
              alt="img"
              src={src}
              style={{
                position: "absolute",
                right: 0,
                ...styleBillboard,
              }}
            />
          </GridIt>
        </Grid>
      </Box>
    </>
  </BaseCard>
);

const Chara = () => {
  const [modalValue, setModalValue] = useState(0);
  const [isEmpty, setIsEmpty] = useState(false);
  const [isFill, setIsFill] = useState(false);
  const [isRocket, setIsRocket] = useState(true);
  const [transHow, setTransHow] = useState(["", ""]);

  const backStage = transHow[1].split("backstage@roobet.com");
  const snoopStage = transHow[1].split("snoop@roobet.com");

  useEffect(() => {
    if (modalValue === 0) {
      return;
    }

    const translateValues = async () => {
      const transTexts = await translateTexts(modalTexts, modalValue);
      // @ts-ignore
      setTransHow(transTexts);
    };

    translateValues();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [modalValue]);

  useEffect(() => {
    const myRocket = document.querySelector(".mainRocket");
    if (!isEmpty || !isFill || myRocket == null) {
      return;
    }

    myRocket.src = emptyRocket;
    myRocket.classList.add("firstRocket");

    setInterval(() => {
      const customVal = myRocket.getAttribute("custom-attr");
      const customNum = parseInt(customVal);
      myRocket.src = customNum === 3 ? emptyRocket : fillRocket;

      const myCustom = customVal != null && customNum < 7 ? customNum + 1 : 0;
      myRocket.setAttribute("custom-attr", myCustom.toString());
    }, 500);
  }, [isEmpty, isFill]);

  return (
    <>
      <MainCard
        src="https://cdn.jsdelivr.net/gh/violet-guru/g2/d/001slash.png"
        styleBillboard={{ bottom: "0", width: "675px" }}
        buttonVal="ROCK ON"
        actionFile=""
        termsFile="roo-rocks-the-cosmos"
        setModalVal={setModalValue}
        isBigTop
      >
        <GridIt>May 15th - 28th</GridIt>
        <GridIt>
          <Box sx={titleSec}>Mr. Roo</Box>
          <Box sx={titleYe}>Rocks the Cosmos</Box>
          <ul style={unList}>
            <li>100 lucky winners and $100,000 in prizes</li>
            <li>Every $500 wagered on Roobet gets you a ticket</li>
            <li>
              Play{" "}
              <a
                style={{ textDecoration: "revert" }}
                href="https://roobet.com/crash"
                target="_blank"
                rel="noreferrer"
              >
                Crash
              </a>{" "}
              and earn 5x the tickets
            </li>
          </ul>
        </GridIt>
      </MainCard>
      <Box
        sx={{
          textAlign: "center",
          maxWidth: "80ch",
          margin: "auto",
          marginBottom: f130,
          position: "relative",
        }}
      >
        <Box
          sx={{
            position: "absolute",
            top: "-30px",
            right: "-100px",
            width: "300px",
            display: { xs: "none", lg: "block" },
          }}
        >
          <img
            alt="img"
            style={{ width: "100%" }}
            src="https://cdn.jsdelivr.net/gh/violet-guru/g2/d/002rocks.png"
          />
        </Box>
        <Box sx={titleSec}>Mr. Roo</Box>
        <Box sx={titleYe}>Rocks the Cosmos</Box>
        <p>
          Join Mr. Roo aboard the Metallic Maiden and set the warp drive for
          uncharted galactic destinations filled with rockin riches beyond your
          wildest dreams.
        </p>
        <p>
          Amp it up to 11 and break the silence of the void, blast some noise
          through the galaxies, and Rock the Cosmos!
        </p>
      </Box>
      <MainCard
        src="https://user-images.githubusercontent.com/284717/234648059-5f81b904-5fd9-4695-bd80-3c738f6d75da.png"
        styleBillboard={{ bottom: "0", width: "700px" }}
        buttonVal="PLAY NOW"
        actionFile=""
        termsFile="astro-roo"
        setModalVal={setModalValue}
      >
        <GridIt>May 17th</GridIt>
        <GridIt>
          <Box component="span" sx={titleSec}>
            Astro{" "}
          </Box>
          <Box component="span" sx={titleYe}>
            Roo
          </Box>
        </GridIt>
        <GridIt>Reach for the stars and stack up gold bars!</GridIt>
        <GridIt>
          <ul style={unList}>
            <li>Play Crash</li>
            <li>Reach the highest multiplier and become Astro Roo</li>
            <li>
              Get a piece of all Crash wagers until someone beats your score
            </li>
          </ul>
        </GridIt>
      </MainCard>
      <MainCard
        src="https://user-images.githubusercontent.com/284717/234648067-c9726e76-641a-40ef-9ae6-d63dea267dcc.png"
        styleBillboard={{ bottom: "0", width: "620px" }}
        buttonVal="BLAST OFF"
        actionFile=""
        termsFile="astro-roo"
        setModalVal={setModalValue}
        isBigBottom
      >
        <GridIt>May 24th</GridIt>
        <GridIt>
          <Box component="span" sx={titleSec}>
            Astro{" "}
          </Box>
          <Box component="span" sx={titleYe}>
            Roo
          </Box>
        </GridIt>
        <GridIt>Become Astro Roo, and watch the winnings flow to you!</GridIt>
        <GridIt>
          <ul style={unList}>
            <li>Play Crash on May 24th</li>
            <li>Reach the highest multiplier and become Astro Roo</li>
            <li>
              Get a piece of all Crash wagers until someone beats your score
            </li>
          </ul>
        </GridIt>
      </MainCard>
      <Box
        sx={{
          textAlign: "center",
          position: "relative",
          borderRadius: f20,
          backgroundSize: "cover",
          backgroundImage:
            "url(https://user-images.githubusercontent.com/284717/234601852-ff66999a-a342-4fdc-bacb-a79690e1a49e.png)",
        }}
      >
        <Box
          sx={{
            display: { xs: "block", lg: "none" },
          }}
        >
          <img
            alt="img"
            style={{ width: f200, marginTop: "-70px" }}
            src="https://user-images.githubusercontent.com/284717/234433356-76fbb5b0-84fc-4cfa-a0f6-1fafdad1295f.png"
          />
        </Box>
        <BaseImage
          alt="img"
          src="https://cdn.jsdelivr.net/gh/violet-guru/g2/d/004roo.svg"
          style={{
            width: "40%",
            minWidth: "330px",
            marginTop: f130,
          }}
        />
        More events coming soon.
        <Box sx={{ marginTop: f100, fontWeight: "bold", fontSize: f45 }}>
          GOOD LUCK. HAVE FUN.
        </Box>
        <Box sx={{ marginTop: f20 }}>
          Let the good times roll, but gamble with control.
        </Box>
        <GrandButton
          sx={{ marginTop: f40, marginBottom: f60 }}
          onClick={goRoot}
        >
          HOP IN
        </GrandButton>
        <Box
          sx={{
            position: "absolute",
            top: "-120px",
            right: "60px",
            width: "300px",
            display: { xs: "none", lg: "block" },
          }}
        >
          <img
            alt="img"
            style={{ width: "100%" }}
            src="https://user-images.githubusercontent.com/284717/234433356-76fbb5b0-84fc-4cfa-a0f6-1fafdad1295f.png"
          />
        </Box>
        <Box
          sx={{
            position: "absolute",
            bottom: "30px",
            left: "35px",
            width: "310px",
            display: { xs: "none", lg: "block" },
          }}
        >
          <img
            alt="img"
            style={{ width: "100%" }}
            src="https://user-images.githubusercontent.com/284717/234433357-e6f0597b-1673-482d-8290-36f653a355b4.png"
          />
        </Box>
        <Box
          sx={{
            display: { xs: "block", lg: "none" },
          }}
        >
          <img
            alt="img"
            style={{ width: f200, marginBottom: f40 }}
            src="https://user-images.githubusercontent.com/284717/234433357-e6f0597b-1673-482d-8290-36f653a355b4.png"
          />
        </Box>
      </Box>
      {isRocket && (
        <img
          className="mainRocket"
          alt="img"
          onClick={() => setIsRocket(false)}
        />
      )}
      <Box sx={{ display: "none" }}>
        <img alt="img" src={emptyRocket} onLoad={() => setIsEmpty(true)} />
        <img alt="img" src={fillRocket} onLoad={() => setIsFill(true)} />
      </Box>
      <Modal
        open={modalValue > 0}
        onClose={() => {
          setModalValue(0);
        }}
      >
        <ModalDialog
          size="lg"
          variant="outlined"
          sx={{
            background: "linear-gradient(to bottom, lightgray, white 65%)",
            ...dialogModal,
          }}
        >
          <ModalClose />
          <Grid
            container
            spacing={3}
            alignItems="center"
            direction="column"
            sx={{ marginTop: "7px", marginBottom: f20 }}
          >
            <GridIt>
              <LotAnim
                src="https://cdn.jsdelivr.net/gh/violet-guru/f/b/dope_lottie.json"
                style={{ width: "310px" }}
              />
            </GridIt>
            <GridIt sx={titleSec}>{transHow[0]}</GridIt>
            <GridIt>
              <UnList>
                <li>
                  {modalValue === modal1 ? (
                    <>
                      {backStage[0]}
                      <AnLink href="mailto:backstage@roobet.com">
                        backstage@roobet.com
                      </AnLink>
                      {backStage[1]}
                    </>
                  ) : (
                    <>
                      {snoopStage[0]}
                      <AnLink href="mailto:snoop@roobet.com">
                        snoop@roobet.com
                      </AnLink>
                      {snoopStage[1]}
                    </>
                  )}
                </li>
                <li>{transHow[trans2]}</li>
                <li> {transHow[trans3]}</li>
              </UnList>
            </GridIt>
            <GridIt onClick={goRoot}>
              <GrandButton>{transHow[trans4]}</GrandButton>
            </GridIt>
          </Grid>
        </ModalDialog>
      </Modal>
    </>
  );
};

export default memo(Chara);
