export const translateTexts = async (modalVals, modalValue) => {
  try {
    const currentLang = Weglot.getCurrentLang();
    const payLoad = {
      words: [
        { t: 1, w: "How To Enter" },
        ...modalVals[modalValue - 1],
        { t: 1, w: "PLAY NOW" },
      ],
      languageTo: currentLang,
    };

    // Sending the same payload twice, Weglot API bug
    await Weglot.translate(payLoad, () => {});
    let returnVal;
    await Weglot.translate(payLoad, (dataValues) => {
      returnVal = dataValues;
    });
    return returnVal;
  } catch (myError) {
    console.error(myError);
  }
};

// https://stackoverflow.com/a/31615643/6227407
export const getNumberWithOrdinal = (n) => {
  const a = 100;
  const b = 20;
  const c = 10;
  const s = ["th", "st", "nd", "rd"],
    v = n % a;
  return n + (s[(v - b) % c] || s[v] || s[0]);
};

